.text {
    text-align: center;
    font-size: 18px;
    padding: 15px 0;
    margin: 0 20%;
    border-radius: 10px;
    border: 1px solid #E8E8E8;
}
.general > div:first-child {
    margin-top: 40px;
}
.general > div:last-child {
}

.part {
    padding: 15px 0;
    margin: 30px 40%;
    text-align: center;
    font-size: 16px;
    border: 1px solid #9C9C9C;
}
.part:hover {
    color: #9C9C9C;
    cursor: pointer;
}

.form {
    text-align: center;
}
.form3 {
    text-align: center;
    margin-top: -40px;
}
.main {
    width: 20%;
    padding: 50px 0;
    display: inline-block;
}
.input, .gender, .divError  {
    padding: 10px 0;
}
.gender > div {
    display: inline;
    padding: 0 10px;
}
.divError {
    color: brown;
}
.but {
    border-radius: 10px;
    width: 80%;
    margin-top: 30px;
    height: 30px;
    background-color: #E8E8E8;
    border: #9C9C9C solid 1px;
    outline: none;
    cursor: pointer;
}
.but:hover {
    background-color: #9C9C9C;
}


.but:hover {
    background-color: #9C9C9C;
}

@media screen and (max-width: 1000px) {
    .text {
        font-size: 16px;
        padding: 10px 0;
        margin: 0 15%;
    }
    .part {
        margin: 30px 25%;
        font-size: 15px;
    }
    .main {
        width: 45%;
        padding: 50px 0;
        display: inline-block;
    }
}
